import React, { Component } from 'react';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import ButtonToolbar from 'react-bootstrap/ButtonToolbar';

import './Feature.css';

class Feature extends Component {

    render() {
        return (
            <section id={this.props.id}>

                <Container>

                    <Row data-aos="fade-up" data-aos-duration="1500">

                        <Col md className="text-md-left mt-5 my-auto">

                            <div>
                                <h1 className="display-1">{this.props.title}</h1>
                                <p className="lead">{this.props.content}</p>
                            </div>

                            <ButtonToolbar className='my-5'>
                                <Col lg className='pr-lg-2'>
                                    <Button block variant='dark' size='lg' href={this.props.href} className='drop-shadow mb-2 mb-lg-0'>Download</Button>
                                </Col>
                                <Col lg className='pl-lg-2'>
                                    <Button block variant='light' size='lg' href='#app-img' className='mt-2 mt-lg-0'>Discover more</Button>
                                </Col>
                            </ButtonToolbar>

                        </Col>

                        <Col md className="text-center align-middle">
                            {this.props.display}
                        </Col>

                    </Row>

                </Container>

            </section>
        );
    }

}

export default Feature;